(function() {
    'use strict';

    angular
        .module('MeuTucano')
        .controller('SenhaListController', SenhaListController);

    function SenhaListController(Senha, TableHeader, $stateParams, Restangular, ngDialog, toaster) {
        var vm = this;

        /**
         * Cabealho da tabela
         * @type {TableHeader}
         */
        vm.tableHeader = TableHeader.init('senhas', vm);

        vm.load = function() {
            vm.loading = true;

            Senha.fromUser($stateParams.id, {
                page:     vm.tableHeader.pagination.page,
                per_page: vm.tableHeader.pagination.per_page,
            }).then(function(response) {
                vm.tableData = response;
                vm.loading   = false;
            });
        };
        vm.load();

        /**
         * Abre o formulrio de senha
         *
         * @return {void}
         */
        vm.openForm = function(senha) {
            ngDialog.open({
                template: 'views/usuario/senha/form.html',
                controller: 'SenhaFormController',
                controllerAs: 'SenhaForm',
                data: {
                    senha: senha || { usuario_id: $stateParams.id }
                }
            }).closePromise.then(function(data) {
                if (data.value === true) vm.load();
            });
        };

        /**
         * Remove a senha
         *
         * @param  {int} senha
         * @return {void}
         */
        vm.destroy = function(id) {
            Senha.delete(id).then(function() {
                toaster.pop('success', 'Sucesso!', 'Senha deletada com sucesso!');
                vm.load();
            });
        };

        /**
         * Esconde / mostra a senha
         * @param  {int} index indice da senha
         * @return {void}
         */
        vm.toglePassword = function(index) {
            if (vm.tableData.data[index].inputType == 'text') {
                vm.tableData.data[index].inputType = 'password';
            } else {
                vm.tableData.data[index].inputType = 'text';
            }
        };
    }
})();